header h1 {
  color: #f2f2f2;
}

.wrapper {
  position: relative;
  margin-bottom: 1rem;
  margin-top: 3rem;
}

/*style label to give some more space*/
.wrapper label {
  display: block;
  padding: 15px 0 12px 35px;
  color: #fff;
  font-weight: bold;
  font-size: 1.8rem;
}

/*style and hide original checkbox*/
.wrapper input {
  height: 25px;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 15px;
}

/*position new box*/
.wrapper input + label::before {
  border: 2px solid;
  content: "";
  height: 20px;
  left: 0;
  position: absolute;
  top: 20px;
  width: 20px;
}

/*svg background styles*/
.wrapper input + label::after {
  content: "";
  opacity: 0;
  background: #fff;
  height: 12px;
  width: 12px;
  position: absolute;
  left: 6px;
  top: 26px;
  transition: opacity 0.2s ease-in-out;
}

/*reveal check for 'on' state*/
.wrapper input:checked + label::after {
opacity: 1;
}

/*focus styles*/
.wrapper input:focus + label::before {
box-shadow: 0 0 0 3px #ffbf47;  
outline: 3px solid transparent; /* For Windows high contrast mode. */

}

@media (max-width: 767px) {
  header {
    height: 8rem;
  }

  .logo {
    left: auto;
    right: 1.5rem;
  }

  .wrapper {
    margin-top: 3rem;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .wrapper label {
    font-size: 1.6rem;
  }
}
